<template>
  <div class="resultsteps-container">
    <div class="resultsteps-box">
      <NavBar />
      <span class="resultsteps-box--title">结果详情</span>
    </div>
    <van-steps direction="vertical" :active="0">
      <van-step>
        <h3>提现发起成功，等待银行处理</h3>
        <p>{{ times.createTime }}</p>
      </van-step>
      <van-step>
        <h3>预计到账时间</h3>
        <p>{{ times.estimateTime }}</p>
      </van-step>
    </van-steps>
    <p class="action-content">到账时间取决于银行，如长时间未到账，烦请咨询银行</p>
    <van-goods-action>
      <van-button to="/home" class="result-btn" size="large">完成</van-button>
    </van-goods-action>
  </div>
</template>
<script>
import NavBar from '../navbar'
import { aboutTime } from '../../api/index'
import { Steps, Step, Button, GoodsAction } from 'vant'
export default {
  name: 'resultsteps',
  components: {
    NavBar,
    [Steps.name]: Steps,
    [Step.name]: Step,
    [Button.name]: Button,
    [GoodsAction.name]: GoodsAction
  },
  data() {
    return {
      times: {} //预估时间
    }
  },
  created() {
    this.getTime()
  },
  methods: {
    async getTime() {
      let time = await aboutTime()
      this.times = time.data
    }
  }
}
</script>
<style lang="less">
.resultsteps-container {
  .resultsteps-box {
    background: url(../../assets/images/ssg/pass_word.png) no-repeat;
    background-size: 100%;
    width: 100%;
    font-size: @base-size * 1.4;
    box-sizing: border-box;
    padding-bottom: @base-size*2;
    &--title {
      font-size: @base-size*2.8;
      color: @ft-white;
      margin: 20px 0px 10px 20px;
    }
  }

  .action-content {
    color: @gradient-color-ssg;
    font-size: @base-size*1.2;
    margin-left: @base-size*2;
  }
  .result-btn {
    width: 90%;
    display: block;
    margin: @base-size*2 auto;
    font-size: @base-size*1.8;
    color: @ft-color-ssg;
    background-color: @btn-color-ssg;
    border-radius: @base-size*5;
  }
}
</style>
